"use client";

import { useRouter, useSearchParams } from "next/navigation";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import type { IAccount } from "@/lib/models";

interface TransactionFiltersProps {
  accounts: IAccount[];
}

export function TransactionFilters({ accounts }: TransactionFiltersProps) {
  const router = useRouter();
  const searchParams = useSearchParams();

  const handleTypeChange = (value: string) => {
    const params = new URLSearchParams(searchParams.toString());
    if (value === "all") {
      params.delete("type");
    } else {
      params.set("type", value);
    }
    router.push(`/dashboard/transactions?${params.toString()}`);
  };

  const handleAccountChange = (value: string) => {
    const params = new URLSearchParams(searchParams.toString());
    if (value === "all") {
      params.delete("account");
    } else {
      params.set("account", value);
    }
    router.push(`/dashboard/transactions?${params.toString()}`);
  };

  return (
    <div className="flex flex-wrap gap-4">
      <Select
        value={searchParams.get("type") || "all"}
        onValueChange={handleTypeChange}
      >
        <SelectTrigger className="w-[180px]">
          <SelectValue placeholder="Filter by type" />
        </SelectTrigger>
        <SelectContent>
          <SelectItem value="all">All Types</SelectItem>
          <SelectItem value="deposit">Deposits</SelectItem>
          <SelectItem value="withdrawal">Withdrawals</SelectItem>
          <SelectItem value="transfer_in">Transfers In</SelectItem>
          <SelectItem value="transfer_out">Transfers Out</SelectItem>
          <SelectItem value="emi_payment">EMI Payments</SelectItem>
          <SelectItem value="loan_disbursement">Loan Disbursements</SelectItem>
        </SelectContent>
      </Select>

      <Select
        value={searchParams.get("account") || "all"}
        onValueChange={handleAccountChange}
      >
        <SelectTrigger className="w-[200px]">
          <SelectValue placeholder="Filter by account" />
        </SelectTrigger>
        <SelectContent>
          <SelectItem value="all">All Accounts</SelectItem>
          {accounts.map((account) => (
            <SelectItem
              key={account._id.toString()}
              value={account._id.toString()}
            >
              {account.accountType} - {account.accountNumber.slice(-4)}
            </SelectItem>
          ))}
        </SelectContent>
      </Select>
    </div>
  );
}
