"use client";

import { useState } from "react";
import { WithdrawDialog } from "./transaction-actions";
import { AccountData } from "@/types/account"; // âœ… Import from shared file

interface Props {
  accounts: AccountData[];
  userId: string;
}

export function WithdrawDialogWrapper({ accounts, userId }: Props) {
  const [open, setOpen] = useState(false);

  const createTransaction = async (data: any) => {
    const response = await fetch("/api/transactions", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(data),
    });
    if (!response.ok) throw new Error("Failed to create transaction");
  };

  const updateAccountBalance = async (
    accountId: string,
    newBalance: number
  ) => {
    const response = await fetch(`/api/accounts/${accountId}`, {
      method: "PATCH",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ balance: newBalance }),
    });
    if (!response.ok) throw new Error("Failed to update balance");
  };

  return (
    <>
      <button
        className="px-4 py-2 rounded bg-accent text-white hover:bg-accent/80"
        onClick={() => setOpen(true)}
      >
        Withdraw
      </button>

      <WithdrawDialog
        accounts={accounts}
        userId={userId}
        open={open}
        onOpenChange={setOpen}
        createTransaction={createTransaction}
        updateAccountBalance={updateAccountBalance}
      />
    </>
  );
}
